import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { AuthCode, Layout } from '../components/atoms/Auth'
import { LabelInput } from '../components/organisms/Auth'
import { WarningMsg } from '../components/atoms/Auth'
import { issuanceCode } from '../utils/issuanceCode'
import { useNavigate } from 'react-router-dom'

let emailRegex = new RegExp('[a-z0-9]+@[a-z]+.[a-z]{2,3}')

export default function SignupPage() {
  const [input, setInput] = useState({
    email: '',
    password: '',
    passwordConfirm: '',
  })
  const [isActive, setIsActive] = useState(false) //    
  const [isDuplicate, setIsDuplicate] = useState(false) //  
  const [isValidate, setIsValidate] = useState(false) //   
  const [isConfirm, setIsConfirm] = useState(false) //    
  const [isOpen, setIsOpen] = useState(false) //    
  const [isDone, setIsDone] = useState(false) //    
  const [code, setCode] = useState(null) //   
  const navigate = useNavigate()

  //        
  useEffect(() => {
    if (
      !!input['email'] &&
      !!input['password'] & !!input['passwordConfirm'] &&
      isConfirm &&
      isDone
    ) {
      setIsActive(true)
    } else {
      setIsActive(false)
    }

    setIsValidate(emailRegex.test(input['email']))
  }, [{ ...input }])

  useEffect(() => {
    cofirmPassword()
  }, [input['password'], input['passwordConfirm']])

  useEffect(() => {
    setIsOpen(false)
    setIsDuplicate(false)
  }, [input['email']])

  const handleOnchange = (e) => {
    const { name, value } = e.target
    setInput({ ...input, [name]: value })
  }

  //   
  const CheckDuplicates = async () => {
    setIsOpen(true)
    const testEmail = 'user1@mail.com'
    try {
      if (input['email'] === testEmail) {
        setIsDuplicate(true)
      } else {
        setIsDuplicate(false)
        issuanceCode(setCode)
      }
    } catch (err) {
      console.log(err)
    }
  }

  //  
  const cofirmPassword = () => {
    if (input['password'] === input['passwordConfirm']) {
      setIsConfirm(true)
    } else {
      setIsConfirm(false)
    }
  }

  //  
  const submitSignup = () => {
    navigate('/MAKE-RE_ver2/done')
  }

  return (
    <Layout>
      <Title></Title>
      <Form id="signupForm" method="POST" onSubmit={submitSignup}>
        <EmailWrap id="signupEmailForm" isValidate={isValidate}>
          <LabelInput
            title=""
            name="email"
            type="email"
            handleOnChange={(e) => {
              handleOnchange(e)
            }}
            warning={isDuplicate}
          />
          <Button
            form="signupForm"
            type="button"
            onClick={CheckDuplicates}
            disabled={!isValidate}
          >
            
          </Button>
        </EmailWrap>
        {isOpen ? (
          <AuthCode
            warning={isDuplicate}
            alertMsg={'   .'}
            isDone={isDone}
            setIsDone={setIsDone}
            code={code}
            setCode={setCode}
          />
        ) : null}
        <LabelInput
          title=""
          name="password"
          type="password"
          handleOnChange={(e) => {
            handleOnchange(e)
          }}
        />

        <LabelInput
          title=" "
          name="passwordConfirm"
          type="password"
          handleOnChange={(e) => {
            handleOnchange(e)
          }}
          warning={!isConfirm}
        />
        {!isConfirm ? (
          <WarningMsg>   .</WarningMsg>
        ) : null}
      </Form>
      <Notice>
          14 , 
        <br /> <span> , </span> .
      </Notice>
      <Button
        form="signupForm"
        isActive={isActive}
        disabled={!isActive}
        type="submit"
      >
         
      </Button>
    </Layout>
  )
}

const Title = styled.p`
  font-size: 32px;
  font-weight: 600;
  color: #121314;
  margin-bottom: 60px;
`

const Form = styled.form`
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  gap: 16px;
`

const EmailWrap = styled.div`
  position: relative;
  display: flex;
  gap: 8px;

  button {
    position: absolute;
    top: 16px;
    right: 0;
    width: 80px;
    height: 42px;
    background-color: #d9dbe0;
    border-radius: 10px;
    font-size: 14px;
    font-weight: 500;
    background-color: ${({ isValidate }) =>
      isValidate ? '#2e6ff2' : '#d9dbe0'};
    color: ${({ isValidate }) => (isValidate ? '#fff' : '#8D9299')};
  }
`

const Notice = styled.p`
  font-size: 14px;
  line-height: 20px;
  color: #47494d;
  font-weight: 500;
  text-align: center;
  margin: 24px 0;

  span {
    color: #2e6ff2;
  }
`

const Button = styled.button`
  width: 100%;
  height: 42px;
  border-radius: 10px;
  font-weight: 500;
  font-size: 14px;
  background-color: ${({ isActive }) => (isActive ? '#2e6ff2' : '#d9dbe0')};
  color: ${({ isActive }) => (isActive ? '#fff' : '#8d9299')};
`
