import { useState, useEffect, useRef, useContext } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import Logo from '../../../assets/Logo.svg'
import ToggleBtn from '../../atoms/Button/ToggleBtn'
import ColorIcon from '../../atoms/ColorIcon/ColorIcon'
import LicatFace from '../../../assets/icon-liacat.svg'
import { ResumeContext } from '../../../context/ResumeContext'
import { MainBtn } from '../../atoms/Button'
import * as styles from './Header-style'

export default function Header({ options }) {
  const { resumeData } = useContext(ResumeContext)
  const profileImg = resumeData['profile']['profileImg']
  const { isCenter, hasCreate, hasProfile, isWhite } = options
  const [isMenuOpen, setMenuOpen] = useState(false)
  const navigate = useNavigate()

  const handleToggleMenu = () => {
    setMenuOpen(!isMenuOpen)
  }

  const moveWrite = () => {
    navigate('/MAKE-RE_ver2/write')
  }

  //      
  const menuRef = useRef(null)

  useEffect(() => {
    const handleClickOutside = (e) => {
      if (menuRef.current && !menuRef.current.contains(e.target)) {
        setMenuOpen(false)
      }
    }
    document.addEventListener('mousedown', handleClickOutside)
  }, [menuRef])

  return (
    <styles.HeaderCont isWhite={isWhite}>
      <styles.HeaderLayout isCenter={isCenter}>
        {isCenter ? (
          <h1>
            <Link to="/MAKE-RE_ver2/">
              <ColorIcon
                iconPath={Logo}
                width="176px"
                height="46px"
                type="logo"
              />
            </Link>
          </h1>
        ) : (
          <>
            <h1>
              <Link to="/MAKE-RE_ver2">
                <ColorIcon
                  iconPath={Logo}
                  width="176px"
                  height="46px"
                  type="logo"
                />
              </Link>
            </h1>
            <styles.BtnCont>
              {hasCreate && (
                <MainBtn type="create" onClick={moveWrite}>
                   
                </MainBtn>
              )}
              {hasProfile && (
                <styles.ProfileBtn
                  onClick={handleToggleMenu}
                  isMenuOpen={isMenuOpen}
                >
                  <img alt="" src={profileImg || LicatFace} />
                  {isMenuOpen && (
                    <styles.MenuList ref={menuRef}>
                      <styles.MenuItem>
                        <Link to="/MAKE-RE_ver2/myresume"></Link>
                      </styles.MenuItem>
                      <styles.MenuItem>
                        <button></button>
                      </styles.MenuItem>
                    </styles.MenuList>
                  )}
                </styles.ProfileBtn>
              )}
              <ToggleBtn />
            </styles.BtnCont>
          </>
        )}
      </styles.HeaderLayout>
    </styles.HeaderCont>
  )
}
