import React, { useState } from 'react'
import styled from 'styled-components'
import WarningMsg from './WarningMsg'
import { Input, Button } from './index'
import { issuanceCode } from '../../../utils/issuanceCode'
import EmailAuthCheck from '../../../assets/icon-email-auth.svg'
import HelpCircle from '../../../assets/icon-help-circle.svg'

export default function AuthCode({
  warning,
  alertMsg,
  isDone,
  setIsDone,
  code,
  setCode,
}) {
  const [inputCode, setInputCode] = useState(null) //  
  const [isActive, setIsActive] = useState(true) //    
  const [isOpen, setIsOpen] = useState(false) //    

  const sendCode = () => {
    setIsOpen(true)

    if (inputCode === code) {
      setIsDone(true)
      setInputCode(code)
    } else {
      setIsDone(false)
      setInputCode('')
    }

    setIsActive(true)
  }

  const handleCode = (e) => {
    const { value } = e.target
    setInputCode(value)

    if (value.length === 5) {
      setIsActive(false)
    } else {
      setIsActive(true)
    }
  }

  //  
  const getAuthCode = () => {
    issuanceCode(setCode)
  }

  return (
    <>
      {warning ? (
        <WarningMsg>{alertMsg}</WarningMsg>
      ) : (
        <Wrap>
          <Cont>
            <p className="title"> </p>
            <p className="description">
                  :)
            </p>
            <InputWrap>
              <Input
                id="authCode"
                name="authCode"
                type="text"
                value={inputCode}
                onChange={(e) => handleCode(e)}
                disabled={isDone}
              />
              <Button type="button" onClick={sendCode} disabled={isActive}>
                
              </Button>
            </InputWrap>
            <div style={{ display: isOpen ? 'block' : 'none' }}>
              {isDone ? (
                <p className="alert done">   :)</p>
              ) : (
                <p className="alert warning">   .</p>
              )}
            </div>
          </Cont>
          <ResendWrap>
            <p>  ?</p>
            <button type="button" onClick={getAuthCode}>
              <a></a>
            </button>
          </ResendWrap>
        </Wrap>
      )}
    </>
  )
}

const Wrap = styled.div``

const Cont = styled.div`
  display: flex;
  flex-direction: column;
  width: 322px;
  gap: 12px;
  padding: 20px;
  border-radius: 10px;
  margin: 0 0 12px 0;
  border: 1px solid #d9dbe0;

  p.title {
    font-weight: 700;
    font-size: 16px;
    color: #121314;
  }

  p.description {
    font-size: 14px;
    color: #47494d;
  }

  p.alert {
    font-size: 14px;
    font-weight: 500;
  }

  p.done {
    display: flex;
    align-items: center;
    gap: 4px;
    color: #2e6ff2;

    &::before {
      content: url(${EmailAuthCheck});
    }
  }

  p.warning {
    color: #ff3440;
  }
`

const InputWrap = styled.div`
  display: flex;
  gap: 8px;
  button.submit {
  }
`

const ResendWrap = styled.div`
  display: flex;
  gap: 8px;
  align-items: center;
  p {
    display: flex;
    align-items: center;
    gap: 4px;
    font-size: 12px;
    color: #47494d;

    &::before {
      content: url(${HelpCircle});
    }
  }

  a {
    font-size: 12px;
    font-weight: 700;
    color: #2e6ff2;
    cursor: pointer;
  }
`
