import React, { useContext } from 'react'
import { LocalContext } from '../../../pages/PreviewPage'
import { PreviewProfileItem } from '../../atoms/PreviewItem'
import styled from 'styled-components'
import ColorContext from '../../../context/ColorContext'

export default function ProfilePreview() {
  const { data } = useContext(LocalContext)
  const { mainColor } = useContext(ColorContext)
  const profileData = data.profile
  const commitUrl = data.github && data.github[1]

  return (
    <ProfileSection>
      {profileData?.profileImg && (
        <ProfileImg mainColor={mainColor}>
          <img src={profileData?.profileImg} alt="" />
        </ProfileImg>
      )}

      <ProfileBox mainColor={mainColor}>
        <span>
          <strong>{profileData?.name}</strong>
          {profileData?.enName}
        </span>
        <DataList>
          {/*  */}
          {profileData?.phoneNumber && (
            <PreviewProfileItem
              title=""
              content={profileData?.phoneNumber}
            ></PreviewProfileItem>
          )}

          {/*  */}
          {profileData?.fullEmail !== '@' && (
            <PreviewProfileItem
              title=""
              content={profileData?.fullEmail}
            ></PreviewProfileItem>
          )}

          {/*   */}
          {profileData?.blog && (
            <PreviewProfileItem
              title=" "
              type="link"
              content={profileData?.blog}
            ></PreviewProfileItem>
          )}

          {/*  */}
          <PreviewProfileItem
            title=" "
            content={
              profileData.careerLength
                ? `${profileData?.careerLength}`
                : ''
            }
          ></PreviewProfileItem>
          {commitUrl && (
            <img
              src={commitUrl}
              className="commit"
              alt="  "
            />
          )}
        </DataList>
      </ProfileBox>
    </ProfileSection>
  )
}

const ProfileSection = styled.section`
  display: flex;
  gap: 40px;
  page-break-inside: avoid;
  break-inside: avoid;
`

const ProfileImg = styled.div`
  width: 142px;
  height: 142px;
  border-radius: 100px;
  border: ${(props) => `2px solid ${props.mainColor}`};
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  flex-shrink: 0;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
`

const ProfileBox = styled.div`
  width: 100%;

  span {
    display: block;
    margin: 0 0 20px 0;
    padding-bottom: 10px;
    font-weight: 500;
    font-size: 16px;
    line-height: 23px;
    color: ${(props) => props.mainColor};
    border-bottom: ${(props) => `1px solid ${props.mainColor}`};
  }

  span strong {
    color: var(--surface-color);
    font-size: 24px;
    font-weight: 700;
    padding-right: 12px;
  }
`

const DataList = styled.ul`
  font-size: 14px;

  img.commit {
    width: 100%;
    margin-top: 12px;
  }
`
